@mixin gradient-pattern() {
	background: linear-gradient(0.4turn, var(--grad-1), var(--grad-1) 20%, var(--grad-2)), url("../media/pattern.svg");
	background-blend-mode: multiply;
	background-size: cover;
}

@mixin gradient() {
	background: linear-gradient(0.4turn, var(--grad-1), var(--grad-1) 20%, var(--grad-2));
}

@mixin gradient-swapped() {
	background: linear-gradient(0.4turn, var(--grad-2), var(--grad-2) 20%, var(--grad-1));
}

@mixin gradient-stuff() {
	background-clip: border-box;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	-webkit-box-decoration-break: clone;
	box-decoration-break: clone;
	text-decoration: none;
}

:root {

	--grad-1: var(--clr-primary);
	--grad-2: var(--clr-secondary);
	
	[data-gradient="gradient-1"] {
        --grad-1: var(--clr-pink);
        --grad-2: var(--clr-purple);
	}

    [data-gradient="gradient-2"] {
        --grad-1: var(--clr-pink);
        --grad-2: var(--clr-crimson);
	}

	[data-gradient="gradient-3"] {
        --grad-1: var(--clr-crimson);
        --grad-2: var(--clr-red);
    }

    [data-gradient="gradient-4"] {
        --grad-1: var(--clr-orange);
        --grad-2: var(--clr-red);
    }

    [data-gradient="gradient-5"]  {
        --grad-1: var(--clr-yellow);
        --grad-2: var(--clr-tomato);
    }

    [data-gradient="gradient-6"]  {
        --grad-1: var(--clr-yellow);
        --grad-2: var(--clr-lime);
	}
	
    [data-gradient="gradient-7"] {
        --grad-1: var(--clr-lime);
        --grad-2: var(--clr-green);
    }

    [data-gradient="gradient-8"] {
        --grad-1: var(--clr-green);
        --grad-2: var(--clr-cyan);
    }

    [data-gradient="gradient-9"] {
        --grad-1: var(--clr-cyan);
        --grad-2: var(--clr-blue);
	}
	
    [data-gradient="gradient-10"] {
        --grad-1: var(--clr-azure);
        --grad-2: var(--clr-ultramarine);
	}

    [data-gradient="gradient-11"] {
        --grad-1: var(--clr-ultramarine);
        --grad-2: var(--clr-purple);
	}
}

@mixin gradient-url {
	@for $i from 1 through 11 { 
		[data-gradient="gradient-#{$i}"] & {
			fill: url(#gradient-#{$i});
		}
	}
}
