.body--admin {
    display: grid;
}

.main--admin {
    align-items: center;
}
.admin-cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: minmax(200px, 1fr);
    grid-gap: var(--base-2);
    @include fluidify(padding-top, 12px, 64px);
    @include fluidify(padding-bottom, 12px, 64px);

    @media screen and (max-width: $breakpoint-sm) {
        grid-template-columns: 1fr;
    }
}

.admin-list-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: var(--main-gap);
}

.admin-list {
    display: grid;
    //grid-row-gap: var(--base);
    list-style: none;
    padding: 0;
    margin: 0;

    
    &__item {
        display: grid;
        grid-template-columns: 1fr auto;
        grid-template-areas:
            "info buttons";
        padding: var(--base) 0;
        border-top: 1px solid var(--divider);

        @media screen and (max-width: $breakpoint-md) {
            grid-template-columns: 1fr;
            grid-template-areas:
                "info"
                "buttons";
        }
    }

    &__cell {
        &--info {
            grid-area: info;
            color: var(--text-secondary);

            > :first-child {
                color: var(--text-primary);
                font-weight: 700; 
            }
        }
        

        &--buttons {
            display: grid;
            grid-auto-flow: column;
            align-items: center;
            justify-content: start;
            grid-gap: var(--base);
            grid-area: buttons;
        }
    }
}

.main--admin-form {
    margin-bottom: var(--header-height);
}

.admin-form {
    //grid-column: full-start / full-end;
    margin: 0;
    
    &--blog {
        display: grid;
        @include block-layout-reset;
        grid-template-columns: [full-start] minmax(0, 1fr) [wide-start] minmax(0, 1fr) [content-start] minmax(0, 40ch) [middle] minmax(0, 40ch) [content-end] minmax(0, 1fr) [wide-end] minmax(0, 1fr) [full-end];
        
        > p {
            margin: 0;
        }
    }


    &__editor {
        //grid-area: editor;
    }

    &__details {
        //grid-area: details;
    }

    &__title {
        margin-bottom: 0;
        word-wrap: break-word;
    }

    &__desc {
        word-wrap: break-word;
        font-weight: normal;
    }

    &__textarea {
        resize: vertical;
        width: 100%;
        word-wrap: break-word;
        padding: var(--base);
    }

    .quill-wrapper {
        display: flex;
        flex-direction: column;
    }

    .ql-toolbar {
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        z-index: 10;
        background-color: var(--clr-shade-3);
        border: 0 none;
        box-shadow: var(--card-shadow);
        border-radius: 4px;

        @supports (-webkit-backdrop-filter: blur(0px)) {
            -webkit-backdrop-filter: blur(12px);
            background-color: hsla(265, 19%, 21%, .9);
        }
        @supports (backdrop-filter: blur(0px)) {
            backdrop-filter: blur(12px);
            background-color: hsla(265, 19%, 21%, .9);
        }
    }

    .ql-container {
        background-color: var(--card-bg);
        border: 0 none;
        box-shadow: var(--card-shadow);
        border-radius: 4px;
        padding: var(--base-3);
        //min-height: 50vh;
    }

    #tags-output {
        height: 0;
        pointer-events: none;
        visibility: hidden;
        opacity: 0;
    }
}

.quill--will-learn .ql-container  {
    //min-height: 300px;
}

.form-item {
    display: grid;
}

.dialog-buttons {
    display: grid;
    grid-gap: var(--base-2);
    justify-items: center;
}

.gradient-picker {
    --size: 64px;
    display: grid;
    grid-template-columns: repeat(auto-fit, var(--size));
    grid-gap: var(--base);
    padding-bottom: var(--base);

    &__color {

        width: var(--size);
        height: var(--size);
        

        input {
            display: none;

            &:checked {
                + label::before {
                    box-shadow: var(--card-shadow), inset 0 0 0 2px white;
                    content: "✓";
                }
            }
        }

        label {
            display: flex;
            position: relative;
            height: inherit;
            width: inherit;

            &::before {
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                background: linear-gradient(0.4turn, var(--grad-1), var(--grad-1) 20%, var(--grad-2));
                border-radius: 50%;
                box-shadow: var(--card-shadow);
                transition: transform .15s ease-out;
                font-size: 150%;
            }
        }
    }
}

.field-validation-error {
    color: var(--clr-yellow);
}

.svg-picker {
    --size: 200px;
    display: grid;
    grid-template-columns: repeat(auto-fit, var(--size));
    grid-gap: var(--base);
    padding-bottom: var(--base);

    &__item {
        width: var(--size);
        height: var(--size);
        position: relative;
        padding: var(--base-2);

        input {
            display: none;

            &:checked {
                + label::before {
                    box-shadow: var(--card-shadow), inset 0 0 0 2px var(--text-primary);
                }
            }
        }

        label {
            display: flex;
            position: relative;
            height: inherit;
            width: inherit;
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;

            &::before {
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                border-radius: 20px;
                transition: transform .15s ease-out;
                font-size: 150%;
            }
        }

        svg {
            height: 100%;
            fill: url(#gradient);
        }
    }
}