.courses-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(288px,1fr));
    grid-auto-rows: 480px;
    grid-gap: var(--base-2);

    @media screen and (max-width: $breakpoint-sm) {
        grid-auto-rows: auto;
    }
}

.course-card {
    $this: &;
    //--course-card-bg-1: var(--clr-primary);
    //--course-card-bg-2: var(--clr-secondary);
    
    display: grid;
    min-height: 400px;
    cursor: pointer;
    color: var(--text-secondary);

    &:hover {
        #{$this}__content {
            transform: translateY(-12px);
        }

        #{$this}__image {
            opacity: 1;
        }

        #{$this}__title {
            // Gradient stuff
            @include gradient-swapped;
            @include gradient-stuff;
        }
    }

    &__link,
    &__content {
        grid-column: 1 / -1;
        grid-row: 1 / -1;
    }

    &__link {
        z-index: 1;
    }

    &__content {
        background-color: var(--card-bg);
        box-shadow: var(--card-shadow);
        border-radius: 20px;
        display: grid;
        grid-template-rows: 150px auto;
        overflow: hidden;
        transition: transform ease-out 0.15s;
    }

    &__image {
        position: relative;
        
        svg {
            @include gradient-url;
            position: absolute;
            top: var(--base-2);
            left: var(--base-2);
            height: 100%;
            transform-origin: top left;
            transform: scale(0.75);

            stop:nth-child(1) {
                stop-color: var(--grad-1);
            }

            stop:nth-child(2) {
                stop-color: var(--grad-2);
            }

            /* path {
                fill: url(#gradient);
            } */
        }
    }
    
    &__info {
        display: grid;
        grid-template-rows: max-content;
        grid-row-gap: var(--base);
        padding: var(--base-2);
        padding-top: var(--base);

        > * {
            margin: 0;
        }
    }

    &__title {
        margin-top: 0;
        border-radius: 4px;
        position: relative;
        
        // Gradient stuff
        background: linear-gradient(to right, var(--text-primary), var(--text-primary));
        @include gradient-stuff;
    }

    &__title-link {
        text-decoration: none;
    } 

    &__desc {
        //font-size: var(--font-size-small);
        //@include fluidify(font-size, $font-size-min, 0.8*$font-size-max);
    }

    &__teacher {
        display: none;
        align-self: end;
        color: var(--text-disabled);
    }

    &--related {
        min-height: auto;
        
        #{$this}__content {
            grid-template-columns: 96px 1fr;
            grid-template-rows: auto;
            grid-gap: 0;
        }

        #{$this}__image {
            display: flex;
            justify-content: center;
            align-items: center;

            svg {
                position: static;
                height: 60px;
                transform: none;
                transform-origin: center;
            }
        }

        #{$this}__info {
            padding: var(--base-2);
            padding-left: 0;
            grid-gap: var(--base-h);
        }
    }
}
