    .subject-info-box-1,
    .subject-info-box-2 {
    	float: left;
    	width: 45%;

    	select {
			min-height: 200px;
			width: 100%;
    		padding: 0;

    		option {
    			padding: 4px 10px 4px 10px;
    		}

    		option:hover {
    			background: #EEEEEE;
    		}

    	}
    }

    .subject-info-arrows {
    	float: left;
		width: 10%;
		text-align: center;

    	input {
    		width: 70%;
    		margin-bottom: 5px;
    	}
    }